<template>
  <div id="userhomeapp">
    <div class="user-wrap">
      <div class="head-wrap">
        <div class="head">
          <div class="head-img">
            <div class="head-circle" :style="{backgroundImage: 'url(' + circlenow + ')'}">

            </div>
          </div>
          <div class="circle-box" :class="{'circle-box-active':circleFlag}">
            <div class="circle-item"
                 v-for="(item,index) in circle"
                 :key="index"
                 :class="{'circle-box-active':circleFlag}"
                 :style="{backgroundImage: 'url(' + item.src + ')'}"
                 @click="changeCircle(item.src,index)">
              <div class="lock" v-if="islock <= index">
                <div class="cover" :class="{'circle-box-active':circleFlag}">
                  <font-awesome-icon class="icon" :icon="['fas','lock']" />
                </div>
              </div>
            </div>
          </div>
        </div>
        <div class="btn" @click="toggleCicleBox()">Change headframe</div>
      </div>
      <div class="desc">
        <div class="span">ID: {{suerinfo}}</div>
        <div class="span">Level: Environmental newcomer</div>
        <div class="span">Age：
          <span class="Age">
            <input  type="text"  placeholder="Please enter your age" maxlength="3" >
          </span></div>
        <div class="span">Gender：
          <span class="Ages">
         <!--   -->
          <select selected="selected">
            <option value="html" style='display: none'>Choose your gender</option>
            <option value="html">Female</option>
            <option value="css">Male</option>
            <option value="JavaScript">Prefer not to say</option>
          </select>

        </span>
        </div>
        <div class="span"></div>
      </div>
    </div>

    <div class="puzzlehome">
      <div class="puzzle_des">
        <div class="title">Puzzle's difficulty level</div>
        <div class="btn" @click="toGame()">Get more puzzles</div>
      </div>
      <div class="puzzlehome_img">
        <!--    //1-->
        <div class="item">
          <div class="arrowsredlefr item-item red" >
            <div></div>
          </div>

          <div class="reds" >
            <div class="redss">
              <div class="red item-item" v-for="(item, index) in list_red" :key="index" :style="{backgroundImage: 'url(' + item.coverImg + ')'}">
                <div class="red-subitem cover">
                  <!--:style="{backgroundImage: `url('${item.subImg}')`}"-->
                  <font-awesome-icon class="icon"  :icon="['fas','lock']" />
                </div>
              </div>
            </div>
          </div>

          <div class="arrowredright item-item red" >
            <div></div>
          </div>

        </div>
        <!--    //2-->
        <div class="item">
          <div class="arrowleftoringe item-item oringe" >
            <div></div>
          </div>
          <div class="oringes" >
            <div class="oringess">
              <div class="oringe item-item" v-for="(item, index) in list_oringe" :key="index" :style="{backgroundImage: 'url(' + item.coverImg + ')'}">
                <div class="cover">
                  <font-awesome-icon  class="icon" :icon="['fas','lock']" />
                 </div>
              </div>
            </div>
          </div>

          <div class="arrowrightoringe item-item oringe" >
            <div  ></div>
          </div>
        </div>
        <!-- //3-->
        <div class="item">
          <div class="arrowleftyellow item-item yellow">
            <div></div>
          </div>


          <div class="yellows" >
            <div class="yellowss" >
              <div class="yellow item-item"
                   v-for="(item, index) in list_yellow"
                   :key="index"
                   :style="{backgroundImage: 'url(' + item.coverImg + ')'}">
                <div class="cover">
                  <font-awesome-icon class="icon" :icon="['fas','lock']" />
                </div>
              </div>
            </div>
          </div>


          <div class="arrowrightyellow item-item yellow">
            <div></div>
          </div>
        </div>

        <!--  //4-->
        <div class="item" :class="showLeftIcon ? 'active' : ''" style="transition:margin-left 2s">
          <div class="arrowleftgreen item-item green">
            <div v-show="showLeftIcon"  @click="leftClick"></div>
          </div>

          <div class="greens" id="classifyoringes">
            <div class="greenss" style="transition:margin-left .5s" >
              <div class="green item-item classifys-item" v-for="(item, index) in list_green" :ref="`item${index}`"
                   :key="index"
                   :style="{backgroundImage: 'url(' + item.coverImg + ')'}">
                <div class="cover">

                  <font-awesome-icon v-if="item.nums >= kuaijiesuo || false"  class="icon" :icon="['fas','lock']" />

                  <img @click="btnsss(index)" :style="item.AA != '0' || 'display:none;transition:display 1s'" v-else src="./assets/hui.png" style="width: 100%" />

                  <img @click="btnssss(index)" :style="item.BB != '0' || 'transition:display 1s'" style="width: 100%;" :src="item.imgss"   />

                </div>
              </div>
            </div>
          </div>


          <div class="arrowrightgreen item-item green">
            <div v-show="showRightIcon"  @click="rightClick"> </div>
          </div>
        </div>
      </div>
    </div>
    <div class="logout-btn" @click="logout()">Log out</div>
  </div>
</template>

<script>
export default {
  data: () => ({
    suerinfo:sessionStorage.getItem('userinfo'),
    circleFlag: false, // show the circle box?
    circle: [
        {
          src: require("./assets/circle1.png"),
        },
        {
          src: require("./assets/circle2.png"),
        },
        {
          src: require("./assets/circle3.png"),
        },
    ],
    circlenow: '',
    kuaijiesuo:sessionStorage.getItem('lockflagvalue'),
    id: "",
    allPuzzle: 1,
    list_red: [
        {
      coverImg: require("../Puzzle/assets/green.png"),
      text: "Light bulb off",
      subImg: require('./assets/paper.png')
    },
      {
        coverImg: require("../Puzzle/assets/green.png"),
        text: "Reusable tableware",
        subImg: require('./assets/chazi.png')
      },
      {
        coverImg: require("../Puzzle/assets/green.png"),
        text: "Kraft paper bag",
        subImg: require('./assets/bicyck.png')
      },
    ],
    list_oringe: [
      {
        coverImg: require("../Puzzle/assets/yellow.png"),
        text: "Light bulb off",
      },
      {
        coverImg: require("../Puzzle/assets/yellow.png"),
        text: "Reusable tableware",
      },
      {
        coverImg: require("../Puzzle/assets/yellow.png"),
        text: "Kraft paper bag",
      },
    ],
    list_yellow: [
        {
      coverImg: require("../Puzzle/assets/oringe.png"),
      text: "Light bulb off",
    },
      {
        coverImg: require("../Puzzle/assets/oringe.png"),
        text: "Reusable tableware",
      },
      {
        coverImg: require("../Puzzle/assets/oringe.png"),
        text: "Kraft paper bag",
      },
    ],
    list_green: [
        {
        coverImg: require("../Puzzle/assets/red.png"),
        text: "Light bulb off",
        nums:'1',
        imgss:require("./assets/p1.png")
      },
      {
        coverImg: require("../Puzzle/assets/red.png"),
        text: "Reusable tableware",
        nums:'2',
        imgss:require("./assets/p2.png")
      },
      {
        coverImg: require("../Puzzle/assets/red.png"),
        text: "Kraft paper bag",
        nums:'3',
        imgss:require("./assets/p3.png")
      },
      {
        coverImg: require("../Puzzle/assets/red.png"),
        text: "Kraft paper bag",
        nums:'4',
        imgss:require("./assets/p4.png")
      },
      {
        coverImg: require("../Puzzle/assets/red.png"),
        text: "Kraft paper bag",
        nums:'5',
        imgss:require("./assets/p5.png")
      },
      {
        coverImg: require("../Puzzle/assets/red.png"),
        text: "Kraft paper bag",
        nums:'6',
        imgss:require("./assets/p6.png")
      },
    ],
    collected: '', // how many of puzzle
    PuzzleTBcom: '', // 你解锁了多少个物件
    PUZZleTBcl: '',
    islock: '',

    showRightIcon: true, // 是否显示右箭头
    maxClickNum: 0, // 最大点击次数
    lastLeft: 0, // 上次滑动距离
    clickNum: 0, // 点击次数
  }),
  async created() {
    console.log(this.kuaijiesuo,'this.kuaijiesuo')
    this.id = this.$route.query.id;
    this.collected = Number(localStorage.getItem("puzzles"))
    this.PuzzleTBcom = parseInt(Number(localStorage.getItem("puzzles") / 6))
    if(sessionStorage.getItem("lockflagvalue") == 3){
      this.islock = 1
    }
    if(sessionStorage.getItem("lockflagvalue") == 5){
      this.islock = 2
    }
    if(sessionStorage.getItem("lockflagvalue") == 7){
      this.islock = 3
    }


    if(sessionStorage.getItem("gs") == 1){
      this.list_green = JSON.parse(sessionStorage.getItem("green"))
      console.log(this.list_green,'111')
    }


    if(sessionStorage.getItem("gs") != 1){
      console.log('111')
      sessionStorage.setItem('green',JSON.stringify(this.list_green))
      sessionStorage.setItem('gs',1)

    }


  },
  computed: {
    showLeftIcon() {
      return this.clickNum > 0
    },

    /*      showLeftred() {
            return this.clickNum > 0
          }*/
  },
  methods: {

    toggleCicleBox() {
      this.circleFlag = !this.circleFlag
    },
    changeCircle(src, index) {
      if (this.islock > index) { this.circlenow = src } else {
        alert('locked!')
      }

    },
    toGame() {
      this.$router.push('/index/game')
    },
    logout() {

      localStorage.clear()
      sessionStorage.clear()
      this.$router.push('/index/login')
    },
    btnsss(i){
      this.list_green[i].AA = 0;
      sessionStorage.setItem('green',JSON.stringify(this.list_green))

      this.$forceUpdate();
    },
    btnssss(i){
      this.list_green[i].BB = 0;
      if(i == 0){
        this.$router.push(`/index/puzzleintro?id=Non-disposable tableware`)
        sessionStorage.setItem('green',JSON.stringify(this.list_green))
      }
      if(i == 1){
        this.$router.push(`/index/puzzleintro?id=Turn offed light bulb`)
        sessionStorage.setItem('green',JSON.stringify(this.list_green))
      }
      if(i == 2){
        this.$router.push(`/index/puzzleintro?id=Reusable bag`)
        sessionStorage.setItem('green',JSON.stringify(this.list_green))
      }
      if(i == 3){
        this.$router.push(`/index/puzzleintro?id=Scratch Paper`)
        sessionStorage.setItem('green',JSON.stringify(this.list_green))
      }
      if(i == 4){
        this.$router.push(`/index/puzzleintro?id=Turn offed tap`)
        sessionStorage.setItem('green',JSON.stringify(this.list_green))
      }
      if(i == 5){
        this.$router.push(`/index/puzzleintro?id=Bicycle`)
        sessionStorage.setItem('green',JSON.stringify(this.list_green))
      }

      this.$forceUpdate();
    },



    // 点击右箭头（左侧滚动）
    rightClick() {
      // 如果点击次数小于数组长度-1时，执行左滑动效果。
      console.log('11111111')
      console.log(this.clickNum)
      console.log(this.list_green.length)
      if (this.clickNum < this.list_green.length - 3) {
        // 获取当前元素宽度
        console.log(window.innerWidth/100,'window.innerWidth;')
        let width = document.querySelectorAll('.classifys-item')[this.clickNum].offsetWidth + window.innerWidth/100
        console.log('获取当前元素宽度',width)
        // 获取最后一个元素距离左侧的距离
        let lastItemOffsetLeft = (document.getElementsByClassName('classifys-item')[this.list_green.length - 1].offsetLeft) + window.innerWidth/100 *6
        console.log('获取最后一个元素距离左侧的距离',lastItemOffsetLeft)
        console.log('获取最后一个元素距离左侧的距离',lastItemOffsetLeft)
        // 获取可视区域宽度
        const lookWidth = document.getElementById('classifyoringes').clientWidth
        // 如果最后一个元素距离左侧的距离大于可视区域的宽度，表示最后一个元素没有出现，执行滚动效果
        console.log('如果最后一个元素距离左侧的距离大于可视区域的宽度，表示最后一个元素没有出现，执行滚动效果',lookWidth)
        if (lastItemOffsetLeft > lookWidth) {
          // 公示：滚动距离（元素的magin-left值） = 负的它自己的长度 + 上一次滑动的距离
          document.getElementsByClassName('greenss')[0].style.marginLeft = `${-width + this.lastLeft}`+'px'
          this.lastLeft = -width + this.lastLeft
          // 点击次数+1
          this.clickNum += 1
          console.log(this.clickNum,'clckNum')
          // 记录到最后一个元素出现在可视区域时，点击次数的最大值。用于后面点击左侧箭头时判断右侧箭头的显示
          this.maxClickNum = this.clickNum
        }
        /*
          如果最后一个元素距离左侧的距离大于可视距离+自身的宽度，说明没有到最后一个, 显示右侧箭头，否则隐藏右侧箭头
          注意：这里要加上其自身的宽度，否则要多点击一下才会隐藏。因为点击的时候实际上最后一个元素还没有滚动，
          它距离左侧的距离是大于可视区域的（也就是说获取的是上一次点击之后，距离左侧的距离）
          所以这里要加上其自身的宽度
          看不懂的话不用管，反正加上就对了
        */
        this.showRightIcon = lastItemOffsetLeft > lookWidth + width
        console.log('showRightIcon',this.showRightIcon)
      }
    },
    // 点击左箭头（右侧滚动）
    leftClick() {
      // console.log(this.lastLeft, 'lastLeft')
      // console.log(document.querySelectorAll('.classifys-item')[this.clickNum - 1].offsetWidth, '上一个的宽度')
      // 当点击次数大于0时才触发，这样当点击次数clickNum等于1的到时候，clickNum--等于0.根据计算属性的判断会隐藏掉左箭头

      if (this.clickNum > 0) {
        // 获取当前元素宽度
        let width = document.querySelectorAll('.classifys-item')[this.clickNum - 1].offsetWidth + window.innerWidth/100
        // 公示：滚动距离（元素的magin-left值） = 它自己的长度 + 上一次滑动的距离
        document.getElementsByClassName('greenss')[0].style.marginLeft = `${this.lastLeft + width}px`
        this.lastLeft = width + this.lastLeft
        // 点击次数-1
        console.log('lastLeft',this.lastLeft)
        this.clickNum--
        // 如果点击次数小于最大点击次数，说明最后一个元素已经不在可是区域内了，显示右箭头
        if (this.clickNum < this.maxClickNum) {
          this.showRightIcon = true
        }
      }
    }
  }
};
</script>

<style lang="less">
#userhomeapp {
  background: url("./assets/bg.jpg");
  background-position: 50% 50%;
  background-size: cover;
  background-repeat: no-repeat;
  width: 100vw;
  min-height: 100vh;
  margin-top: -4vh;
  padding-top: 4vh;
  @media screen and (min-width: 1024px) {
    padding-top: 10vh;
  }
  .user-wrap {
    background: #ffaaaab3;
    height: 35vh;
    margin: 0 3vw;
    border-radius: 14px;
    display: flex;
    // padding: 4vh 3vw;

    @media screen and (min-width: 1024px) {
      min-height: 30vh;
    }
    @media screen and (max-width: 1024px) {
      height: 31vh;
    }


    .head-wrap {
      margin-right: 10px;
      flex: 1;
      padding: 0vh 10vw;
      padding-top: 94px;

      @media screen and(max-width:1024px) {
        // margin: 0;
        padding-top: 53px;
      }

      .head {
        flex: 1;
        display: block;
        width: 150px;
        height: 150px;

        @media screen and(max-width:1024px) {
          width: 75px;
          height: 75px;
        }

