            background-size: cover;
            transform: translate(-1.1vw, -6.9vh);

            @media screen and(max-width:1024px) {
              transform: translate(-2vw, -4.35vh);
            }
          }
        }

        .circle-box {
          height: 20vh;
          width: 20vw;
          margin-left: -5vw;
          margin-top: 8vh;
          background: #ffaaaab3;
          height: 0;
          transition: .4s all;
          border-bottom-left-radius: 14px;
          border-bottom-right-radius: 14px;
          display: flex;

          @media screen and(max-width:1024px) {
            width: 90vw;
            margin-left: -8vw;
            margin-top: 9vh;
          }
          // visibility: hidden;

          .circle-item {
            width: 8vw;
            background-size: contain;
            background-repeat: no-repeat;
            transition: .4s all;
            height: 0;
            cursor: pointer;
            position: relative;

            @media screen and(max-width:1024px) {
              width: 30vw;

            }

            .cover {
              color: rgba(0, 0, 0, .4);
              font-size: 2rem;
              margin-left: 2.5vw;
              margin-top: 2vw;
              height: 0;
              transition: .4s all;
              // visibility: hidden;
              overflow: hidden;
              max-height: 0;

              @media screen and(max-width:1024px) {
      /*          margin-top: 4.5vh;
                margin-left: 12vw;*/
                margin-top: 4.8vh;
                margin-left: 12.8vw;
              }
            }

            .circle-box-active {
              height: 8vh;
              max-height: 8vh;
              // visibility: visible;
            }

          }

          .circle-box-active {
            height: 15vh;
          }
        }

        .circle-box-active {
          height: 15vh;
        }
      }

      .btn {
        background: rgba(104, 185, 199, 0.8);
        color: #fff;
        /*margin-top: -10px;*/
        // margin-right: 15px
        font-weight: lighter;
        font-size: .5rem;
        border-radius: 14px;
        height: 4vh;
        width: 10vw;
        text-align: center;
        line-height: 4vh;
        cursor: pointer;
        display: block;
        transition: .4s all;
        margin-left: -10px;

        @media screen and(max-width:1024px) {
          width: 30vw;
          margin-left: -20px;
          margin-top: 20px;
        }

        &:hover {
          background: #fff;
          color: rgba(104, 185, 199, 0.8);
        }
      }
    }

    .desc {
      flex: 1;
      margin-top: 8vh;

      @media screen and(max-width:1024px) {
        margin-top: 8vh;
        margin-left: -17vh;
      }

      .span{
        display: block;
        color: #fff;
        font-weight: lighter;
        margin-top: 1vh;
        @media screen and (min-width: 1024px) {
          font-weight: bolder;
          margin-top: 2vh;
        }
        @media screen and (max-width: 1024px) {
          font-weight: bolder;
          margin-top: 0.5vh;
        }
        .Age{
          background: #FFFFFF;
          font-weight: lighter;
          font-size: 0.8rem;
          height: 4vh;
          width: 21vw;
