          }
          input::-webkit-input-placeholder{

            color: #000000;

          }
          input{
            color: #000000;
            font-weight: lighter;
            font-size: 1rem;
            text-align: center;
            width: 19vw;
            @media screen and (max-width: 1024px) {
              height: 3.5vh;
              line-height: 3.5vh;
              width: 30vw;
              font-size: 12px;
            }
          }
        }
        .Ages{
          background: #FFFFFF;
          font-weight: lighter;
          font-size: 0.8rem;
          height: 4vh;
          width: 19.7vw;
          cursor: pointer;
          display: inline-block;
          transition: 0.4s all;
          line-height: 4vh;
          @media screen and (max-width: 1024px) {
            height: 3.5vh;
            width: 30.5vw;
            margin-top: 1.8vw;
            line-height: 3.5vh;
          }
          select{
            background: none;
            font-weight: lighter;
            font-size: 1rem;
            padding-left: 0;
            width: 18vw;
            text-align-last: center;
            -webkit-appearance: none;   /* google */
            -moz-appearance: none;  /* firefox */
