            @media screen and (max-width: 1024px) {
              height: 3.5vh;
              line-height: 3.2vh;
              width: 30vw;
              font-size: 0.5rem;
            }
          }
        }
      }
    }
  }
  .puzzlehome {
    margin-top: 10vh;
    display: flex;
    @media screen and(max-width:1024px) {
      flex-direction: column;
    }

    .puzzle_des {
      width: 40vw;
      height: 60vh;
      background: url('./assets/by.png') 50% 50% no-repeat;
      background-size: contain;
      margin-top: 30vh;
      margin-right: 14vw;

      @media screen and(max-width:1024px) {
        margin-top: 0;
        width: 100vw;
      }

      .title {
        color: #fff;
        transform: translateY(10vh) translateX(12vw);
        font-weight: bold;

        @media screen and(max-width:1024px) {
          // text-align: center;
        }
      }

      .btn {
        height: 5vh;
        color: #fff;
        border: 1px solid #fff;
        width: 10vw;
        text-align: center;
        line-height: 5vh;
        transform: translateY(46vh) translateX(19vw);
        background: rgba(104, 185, 199, 0.8);
        transition: .4s all;
        cursor: pointer;
        font-size: .5rem;
        margin-left: -3%;

        @media screen and(max-width:1024px) {
          width: 60vw;
          transform: translateY(44vh) translateX(19vw);
        }

        &:hover {
          color: rgba(104, 185, 199, 0.8);
          background: #fff;
        }
      }
    }

  }
  .puzzlehome_img {
    width: 40vw;
    height: 100vh;
    background: url('./assets/fz.png') 50% 50% no-repeat;
